import React from 'react';
import Card from './Card';
import rest_data from '../public/api/location/ChIJBUVa4U7P1EAR_kYBF9IxSXY.json';
import '../styles/rest-list.css'

const RestaurantsList = () => {
  const rests = rest_data.data.feedItems;
  return (
    <div className="rest-list">
      {rests.map(rest => <Card rest={rest_data.data.storesMap[rest.uuid]} />)}
    </div>
  );
}

export default RestaurantsList;

