import React from 'react';
import '../styles/card.css';

const Card = ({rest}) => {
  return (
    <div className="card">
      <div className="rest-pic">
        <img src={rest.heroImageUrl} alt={rest.title} />
      </div>
      <div className="rest-name">
        {rest.title}
      </div>
      <div className="rest-category">
        {rest.categories.join(' * ')}
      </div>
      <div className="rest-details">
        <div className='rest-delivery'>
          20 - 30 min.
        </div>
        {rest.feedback && (
          <div className="rest-rating">
            {rest.feedback.rating}
            <span className="star">&nbsp;&#9733;&nbsp;</span>
            {`(${rest.feedback.ratingCount})`}
          </div>
        )}
      </div>

      {rest.endorsement && (
        <div className="rest-endorcement">
          <img src={rest.endorsement.iconUrl}/>
          <span>{rest.endorsement.text}</span>
        </div>
      )}
    </div>
  )
}

export default Card;

